import React, { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAppAsyncDispatch } from '../hooks/store';
import { fetchProgramConfig } from '../store/middlewares/core';
import {
  checkSession, verifyAuth, register, getPerson
} from '../store/middlewares/user';
import { apiErrorHandler, findNextScreen } from '../utils/general';

const LoginCallback = () => {
  const dispatch = useAppAsyncDispatch();
  const navigate = useNavigate();

  useEffect(() => {
    dispatch(verifyAuth()).then(() => {
      dispatch(checkSession())
        .then(({ accessToken }: any) => {
          if (accessToken) {
            dispatch(register({ accessToken }))
              .then(() => {
                dispatch(getPerson()).then((personDetails) => {
                  dispatch(fetchProgramConfig(personDetails.programId));
                  /**
                   * find next screen by kyc details
                   */
                  findNextScreen(personDetails.kyc)
                    .then((nextScreen) => {
                      navigate(nextScreen);
                    });
                });
              }).catch((e) => {
                if (e.code === 'EC_NEW_DEVICE_LOGIN') {
                  navigate('/auth/ssn');
                } else {
                  apiErrorHandler(e);
                }
              });
          }
        });
    }).catch((e) => {
      console.log(e);
      navigate('/');
    });
  }, []);
  return (
    <>
      Loading...
    </>
  );
};

export default LoginCallback;
